<template>
    <el-dialog title="人工干预" :visible.sync="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="自己平台订单号:" :label-width="formLabelWidth">
                <el-input v-model="form.self_order_num" auto-complete="off" :disabled="disabled"></el-input>
            </el-form-item>
            <div class="radio-list">
                <el-form-item label="是否成功:">
                    <el-radio class="radio" v-model="form.is_succ" label="1">成功</el-radio>
                    <el-radio class="radio" v-model="form.is_succ" label="2">失败</el-radio>
                </el-form-item>
            </div>
            <el-form-item label="第三方平台订单号:" :label-width="formLabelWidth">
                <el-input v-model="form.third_order_num" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="备注:" :label-width="formLabelWidth">
                <el-input v-model="form.remark" auto-complete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .radio-list{
            margin-left:120px;
        }
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                dialogForm: false,
                formLabelWidth: '200px',
                disabled:true,
                form:{
                    convert_apply_id: '',
                    self_order_num: '',
                    platform_order_no: '',
                    remark: '',
                    is_succ: '1',
                    message: ''
                }
            }
        },
        methods: {
            showDialog(item) {
                if(item.id){
                    this.form.convert_apply_id = item.id
                } else {
                    this.form.convert_apply_id = '';
                }
                this.form.self_order_num = item.order_bid;
                this.dialogForm = true;
            },
            submit(){
                quRequest.send(quRequest.Apis.qutuiba.convert.confirmApply,this.form
                ).then(res => {
                    this.$message.success('修改成功！');
                }).catch(err => {
                    this.$message.error(`获取分类数据失败：${ err.message || '未知错误' }`);
                    throw err;
                }).done();
            }
        }
    }
</script>


